<template>
    <div class="header">
        <ul class="header-button-left">
            <li @click="step--">Cancel</li>
        </ul>
        <ul class="header-button-right">
            <li v-if="step == 1" @click="step++">Next</li>
            <li v-if="step == 2" @click="publish">ÏûëÏÑ±</li>
        </ul>
        <img src="./assets/logo.png" class="logo" />
    </div>
    <!-- commit(a,b) aÎäî Ïã§ÌñâÌï† Ìï®Ïàò bÎäî storeÏóê Î∞òÎÇ©Ìï† Í∞íÏûÑ -->
    <!--     <button @click="ageUp(100)">ÌïúÏÇ¥ ÎçîÎ®πÍ∏∞</button>
    <h1>{{ $store.state.age }}</h1> -->

    <!--     <p>{{ $store.state.more }}</p>
    <button @click="$store.dispatch('getData')">ÎçîÎ≥¥Í∏∞Î≤ÑÌäº</button> -->
    <!--     <p>{{ name }} {{ age }} {{ ÎÇ¥Ïù¥Î¶Ñ }}</p> -->
    <gram-container
        :gramData="gramData"
        :step="step"
        :uploadImage="uploadImage"
        @write="uploadContent = $event"
        :imageFilter="imageFilter"
    />
    <!-- Î¨¥ÌïúÏä§ÌÅ¨Î°§ Î≥ÄÍ≤Ω ÏòàÏ†ï -->
    <button @click="more" v-if="step == 0">ÎçîÎ≥¥Í∏∞</button>

    <div class="footer" v-if="step == 0">
        <ul class="footer-button-plus">
            <input
                @change="upload"
                accept="image/*"
                type="file"
                id="file"
                class="inputfile"
            />
            <label for="file" class="input-plus">+</label>
        </ul>
    </div>
</template>

<script>
import GramContainer from "./components/GramContainer.vue";
import gramData from "@/gramData";
import axios from "axios";
import { mapMutations, mapState } from "vuex";

export default {
    name: "App",
    components: { GramContainer },
    data() {
        return {
            gramData: gramData,
            moreClick: 0, //state
            step: 0,
            uploadImage: "",
            uploadContent: "",
            imageFilter: "",
            Ïπ¥Ïö¥ÌÑ∞: 0,
        };
    },
    computed: {
        // computed Ìï®ÏàòÎäî ÏÇ¨Ïö©Ìï¥ÎèÑ Ïã§ÌñâÎêòÏßÄÏïäÏùå ÏõπÌéòÏù¥ÏßÄÍ∞Ä Ï≤òÏùå Ïã§ÌñâÌïòÍ≥† Í∞íÏùÑ Í∞ÑÏßÅÌï®
        name() {
            return this.$store.state.name;
        },
        ...mapState(["name", "age", "likes"]),
        ...mapState({ ÎÇ¥Ïù¥Î¶Ñ: "name" }),
    },
    methods: {
        // methods Ìï®ÏàòÎäî ÏÇ¨Ïö©Ìï†ÎïåÎßàÎã§ Ïã§ÌñâÎêòÍ≥† Í∞íÏùÑ Í≥ÑÏÜç Î±âÏùå
        ...mapMutations(["setMore", "likesUp", "ageUp"]),

        more() {
            axios
                .get(
                    `https://codingapple1.github.io/vue/more${this.moreClick}.json`
                )
                .then((result) => {
                    console.log(result);
                    this.gramData.push(result.data);
                })
                .catch((error) => {
                    console.log(error.data);
                });
            if (this.moreClick == 0) {
                this.moreClick += 1;
            } else if (this.moreClick == 1) {
                this.moreClick -= 1;
            }
        },
        upload(e) {
            let uploadFile = e.target.files;
            let uploadUrl = URL.createObjectURL(uploadFile[0]); //ÏóÖÎ°úÎìúÌïú Ïù¥ÎØ∏ÏßÄÎ•º URLÎ°ú ÎßåÎì§Ïñ¥Ï§å
            this.uploadImage = uploadUrl;
            this.step++;
        },
        publish() {
            var myGramData = {
                name: "Kim Hyun",
                userImage: "https://placeimg.com/100/100/arch",
                postImage: this.uploadImage,
                likes: 36,
                date: "May 15",
                liked: false,
                content: this.uploadContent,
                filter: this.imageFilter,
            };
            this.gramData.unshift(myGramData);
            this.step = 0;
        },
    },
    mounted() {
        // emitterÎ°ú Î∞õÏùÄ Îç∞Ïù¥ÌÑ∞Îäî mountedÏóê ÎÑ£Ïñ¥ÎÜìÎäîÍ≤å Í¥ÄÏäµÏù¥Îã§
        this.emitter.on("send", (e) => {
            this.imageFilter = e;
        });
    },
};
</script>

